@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'DM Sans', sans-serif;
}
.btn{
    display: inline-block;
    cursor: pointer;
    border: none;
    border-radius: 5px;
}
.navbar{
    position: fixed;
    display: flex;
    background-color: #fff;
    width: 100%;
    height: 80px;
    z-index: 100;
}
.logo{
    width: 130px;
    height: 70px;
    object-fit: contain;
    margin: 12px 40px;

}
.navbar .nav{
    display: flex;
    margin-bottom: 20px;
}
.navmenu{
   margin: 15px;
   margin-top: 25px;
}
.navmenu .btn{
   background-color: #fff;
   border: 2px solid black;
   font-size: 1rem;
   font-weight: 700;
   font-family: 'DM Sans', sans-serif;
   width: 140px;
   height: 53px;
   margin-top: 0;
   /* margin-bottom: 10px; */
}
.navmenu .skeleton-btn{
    width: 250px;
    height: 50px;
    background-color: #ffc064;
    font-size: 1rem;
    font-weight: 600;
    border: none;
}
.main .main3{
    margin: 0 auto;
}


*{
    box-sizing: border-box;
    font-family: 'DM Sans', sans-serif;
    line-height: 2.5rem; 
}
img{
    width: 500px;
    height: 400px;
    object-fit: contain;
}
.grid{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-gap: 10px;
    gap: 10px;
}
.btn{
    display: inline-block;
    cursor: pointer;
    border: none;
    border-radius: 5px;
}

.main{
    display: flex;
    background-color: #f3f1ee;
    height: 650px;
}
.main img{
    width: 500px;
    height: 400px;
    object-fit: contain;
    justify-self: flex-end;
    margin: 100px;
    margin-right: 20px;
}
.container{
    margin: 130px;
}
.container .heading{
    color: #1b464b;
    font-family: 'DM Sans', sans-serif;
    font-size: 1.3rem;
    line-height: 4rem;
    font-weight: 400;
    padding-bottom:10px ;
}
.container .heading h1{
    padding: 10px 0;
    padding-right: 6px;
}


.container .sub-main span{
    
    color: #00c435;
}
.sub-main{
  margin: 4px;
}
.sub-main .earn{
    margin: 2x 0;
    font-size: 1.6em;
}

.main span{
    color: black;
}

.skeleton-btn{
    width: 225px;
    background-color: #ffc064;
    font-size: 1rem;
    font-weight: 600;
    border: none;
}
.btn{
    border: 2px solid black;
    font-size: 1rem;
    font-weight: 700;
    font-family: 'DM Sans', sans-serif;
    width: 225px;
    height: 50px;
    margin: 5px;
    margin-top: 0px;
    margin-bottom: 40px;
 }
 .container .skill{
     display: flex;
     flex-direction: column;
     margin-bottom: 7px;
 }
 .container .skill .fas{
     display: flex;
    color:#1b464b;
    
 }
 .container .bold{
     color: black;
     font-weight: bold;
 }
 .container .skill h4{
     font-size: 1.6rem;
     color: black;
     margin: 0 5px;
 }
 .main1{
     color: #fff;
     text-align: center;
     height: 500px;
 }
.main2{
    background-color: #f3f1ee;
    text-align: center;
    height: 700px;
}
.main2 .container .heading h1{
    font-size: 1.8rem;
    padding-top: 70px;
    
}
.main2 .container .heading .para{
    font-size: 1.2rem;
    font-weight: 200;
    padding-top: 10px 40px; 
}

.main2 .container .heading .para span{
    color: black;
    font-family: 'DM Sans',sans-serif;
    font-weight: 400;
}

.main2 img{
    width: 150px;
    height: 100px;
    object-fit: contain;
}
.main3{
    display: flex;
    background-color: #033448;
    overflow: hidden;
}
